.seccion__escenarios{
    width: auto;
    min-height: 100vh;
    max-height: max-content;
    background-color: #BED7E4;
}

.seccion__escenarios h2{
    text-align: center;
    padding-top: 40px;
    color: #86BBD8;
}

.escenarios__carrusel{
    position: relative;
    margin-top:  26px;
}

.slide{
    position: relative;
    overflow: hidden;
    width: 100%;
    height: auto;
}

.slide__abierto:checked + .slide__item{
    position: static;
    opacity: 100;
}

.slide__item{
    position: absolute;
    opacity: 0;
    -webkit-transition: opacity 0.6s ease-out;
    transition: opacity 0.6s ease-out;
}

.escenarios{
    display: block;
    margin: auto;
}

.slide__control{
    background: #86BBD8;
    border-radius: 50%;
    color: #3463a8;
    cursor: pointer;
    display: none;
    font-size: 40px;
    height: 40px;
    line-height: 35px;
    position: absolute;
    top: 50%;
    -webkit-transform: translate(0, -50%);
    cursor: pointer;
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);
    text-align: center;
    width: 40px;
    z-index: 10;
}

.slide__control.prev {
    left: 2%;
}
.slide__control.next {
    right: 2%;
}
.slide__control:hover {
    background: #86BBD8;
    color: #aaaaaa;
}
#slide-1:checked ~ .control-1,
#slide-2:checked ~ .control-2,
#slide-3:checked ~ .control-3 {
    display: block;
}
.slide-indicador {
    list-style: none;
    margin-top: 30px;
    padding: 0;
    position:absolute;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    z-index: 10;
    margin-right: 45%;
}
.slide-indicador li {
    display: inline-block;
    margin: 0 5px;
}

.slide-circulo {
    color: #86BBD8;
    cursor: pointer;
    display: block;
    font-size: 35px;
}

.slide-circulo:hover {
    color: rgba(80, 132, 145, 0.856);
}
#slide-1:checked ~ .control-1 ~ .slide-indicador 
     li:nth-child(3) .slide-circulo,
#slide-2:checked ~ .control-2 ~ .slide-indicador 
      li:nth-child(2) .slide-circulo,
#slide-3:checked ~ .control-3 ~ .slide-indicador 
      li:nth-child(1) .slide-circulo {
    color: #12516B;
}

.escenario__h3{
    text-align: center;
    color: #5A8CD7;
    font-weight: 900;
}

.escenario__h6{
    margin: 20px;
    margin-left: 80px;
    margin-right: 80px;
    margin-bottom: 50px;
}